.horizontal{
  display: flex;
  flex-direction: row;

}

.scrolling{
  display: flex;
  flex-direction: column;
}

body {
  background-color: navy;
  margin: 10px;
  display: flex;
  place-items: center;
  min-width: 320px;
  min-height: 100vh;
}

h1 {
  font-family: "Lucida Console", monospace;
  font-size: 3.2em;
  line-height: 1.1;
}

p {
  font-family: "Lucida Console", monospace;
}

.p3 {
  font-family: "Lucida Console", monospace;
}

.list{
  margin:60px;
  color: #dafe11;
  font-family: "Lucida Console", monospace;
  text-decoration: underline;
}

.clicker{
  height: 30px;
  font-family: "Lucida Console", monospace;
  color: darkred !important; 
  background-color: white!important;
  border-color: darkred;
  border-width: 8px;
}

.clicker-container{
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
}

.page3{
  display: flex;
  flex-direction: column;
  margin: auto;
}